import React, { Component } from 'react'
import './../css/dragonPark.css';
import Footer from './Footer';
import {Link} from "react-router-dom";
import Nav from './Nav';
import { connect } from 'react-redux';
import axios from 'axios';
const getProductData = () =>  axios.get('/ds_WaterPark')
                                   .then((res) => res.data);                           
class WaterPark extends Component {
    constructor(props) {
        super(props)
        this.state = {
            data: [],
            numberInit: 1,
            numberEnd: 8,
            flagHandle1 : true,
            flagHandle2 : false,
            dataSearch :''
        }
    }

    componentDidMount() {
        getProductData().then((res) =>{
            console.log(res.recordset);
            this.props.getdataDragonPark(res.recordset)
            this.setState({
                data: res.recordset
            })
          })
    }
    handleClick1 = () => {
        if(this.state.flagHandle2 === true)
        {
            this.setState({
                flagHandle1 : true,
                flagHandle2 : false,
                numberInit : 1,
                numberEnd : 8
            })
        }
    }
    handleClick2 = () => {
        if(this.state.flagHandle1 === true)
        {
            this.setState({
                flagHandle1 : false,
                flagHandle2 : true,
                numberInit : 9,
                numberEnd : 2*8
            })
        }
    }
    getDataSearch = (event) => {
        console.log(event.target.value);
        this.setState({
            dataSearch: event.target.value.trim()
        })
        
    }
    render() {
        var dem = 0;
        return (
            <div>
                <Nav />
                <section className="main">
                    <div className="container">
                        <div className="main-top text-center mt-4">
                            <h3 className="mb-5">Water Park - SANJALIKA WATER PARK</h3>
                        </div>
                        <div className="search-box">
                            <input className="search-txt" onChange={(event) => this.getDataSearch(event)} type="text" name='search' placeholder="Type to search" />
                            <div className="search-btn"><i className="fas fa-search" /></div>
                        </div>
                        <div className="row">
                        {this.state.data.map((val, key) => {
                                if(this.state.dataSearch === null)
                                {
                                    if (val.ID >= this.state.numberInit && val.ID <= this.state.numberEnd ) {
                                        return (
                                            <div className="col-sm-3 box-news mt-4" key={key}>
                                                <img className="img-fluid box-img" src={val.Image} alt="" />
                                                <div className="box-info">
                                                    <Link to={'/WaterParkDetail/' + val.ID} className="box-info-title">{val.Title}</Link>
                                                    <p>Dragon Park | <i className="far fa-calendar-alt mr-2 ml-2" />{val.Date}</p>
                                                    <p>{val.Content}</p>
                                                    <Link to={'/WaterParkDetail/' + val.ID} className="more"> See More &gt;</Link>
                                                </div>
                                            </div>)
                                    }
                                    else {
                                        return '';
                                    }
                                }
                                else
                                {
                                    if(val.Title.toUpperCase().indexOf(this.state.dataSearch.toUpperCase()) !== -1)
                                    {
                                        dem++;
                                        if(dem >= this.state.numberInit && dem <= this.state.numberEnd )
                                        {
                                            return (
                                                <div className="col-sm-3 box-news mt-4">
                                                    <img className="img-fluid box-img" src={val.Image} alt="" />
                                                    <div className="box-info">
                                                        <Link to={'/WaterParkDetail/' + val.ID} href className="box-info-title">{val.Title}</Link>
                                                        <p>Dragon Park | <i className="far fa-calendar-alt mr-2 ml-2" />{val.Date}</p>
                                                        <p>{val.Content}</p>
                                                        <Link to={'/WaterParkDetail/' + val.ID} className="more"> See More &gt;</Link>
                                                    </div>
                                                </div>)
                                        }
                                        else
                                        {
                                            return ''
                                        }
                                           
                                    }
                                    else
                                    {
                                        return '';
                                    }
                                }
                            })}
                        </div>
                        <div className="handleButton mt-5">
                            <div className="handleButton-one" onClick={() =>{this.handleClick1()}}>1</div>
                            <div className="handleButton-two" onClick={() =>{this.handleClick2()}}>2</div>
                        </div>
                    </div>
                </section>
                <Footer />
            </div>
        )
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        getdataDragonPark: (data) => {
            dispatch({
                type:'GET_DATA_DRAGON_PARK',
                getdata: data
            })
        }
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
        prop: state.prop
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(WaterPark);